<script>
import api from '@/api/api'
import rule from '@/libs/rule'
export default {
  name: 'RoleTable',

  data () {
    const validator = rule.spaceTest
    return {
      formValidate: {
        newName: '',
        newDescription: ''
      },
      formValidate1: {
        oldName: '',
        oldDescription: ''
      },
      ruleValidate: {
        newName: [
          { required: true, message: "请输入角色名称", trigger: 'blur' },
          { validator:validator},
          { max: 50, message: "输入字符长度不允许超出50", trigger: 'blur' },
        ],
        newDescription: [
          { required: true, message: "请输入角色描述", trigger: 'blur' },
          {validator:validator},
          { max: 100, message: "输入字符长度不允许超出100", trigger: 'blur' },
        ],
        oldName: [
          { required: true, message: "请输入角色名称", trigger: 'blur' },
          {validator:validator},
          { max: 50, message: "输入字符长度不允许超出50", trigger: 'blur' },
        ],
        oldDescription: [
          { required: true, message: "请输入角色描述", trigger: 'blur' },
          { validator: validator},
          { max: 100, message: "输入字符长度不允许超出100", trigger: 'blur' },
        ]
      },
      searchConName3: '',
      columns1: [
        {
          key: 'name',
          title: "名称",
          // align: 'center'
        },
        {
          key: 'description',
          title: "描述",
          // align: 'center'
        },
        {
          key: 'createDate',
          title: "创建时间",
          // align: 'center'
        },
        {
          key: 'handle',
          title: "操作",
          align: 'center',
        }
      ],
      //操作员数据
      columns2:[
          {
            key: 'roleName',
            title: "操作员名称",
            // align: 'center'
          },
          {
            key: 'isUse',
            title: "状态",
            // align: 'center'
          },
      ],
      handlerLoading:false,//操作员列表loading效果
      handlerDetailLoading:false,//操作员详情loading效果
      readonly:true,
      data3: [],
      initTable3: [],

      ajaxHistoryData: [],
      // 初始化信息总条数
      dataCount: 0,
      // 每页显示多少条
      pageSize: 10,
      pageNumber: 1,
      historyData: [],
      current: 1,
      modal1: false,
      modal2: false,
      
      searchResult: [],
      loading: true,
      selectedItem: [],
      loadingBtn: false,
      modal5:false,
      modal6:false,
      roleName:'',
      data1:[],//操作员列表数据
      isGray:false, // 操作员登录时拿到的operationPermissions的值，默认为false;
      formItem:{
        handlerId:'',
        handleName:'',
        role:'',
        status:'',
        iphone:'',
        email:'',
        createTime:'',
        lastLoginTime:"",
        lastLoginIp:''
      },

      base:1,   // 分辨率自适应 计算后的倍数
      tabHeight:0,  // 分辨率自适应调整：表格的高度
    }
  },
  created () {
    this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true;
    this.getTableList()
    this.init()
  },
  mounted () {
    this.init();
    this.autoFix();
  },
  methods: {
    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      this.tabHeight  = height - 350;
      // this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },

    creatButton () {
      this.$router.push({
        name:'role-create'
      })
    },

    updateRoleInfo(roleParam){
      if(roleParam){
        sessionStorage.setItem('roleParam', JSON.stringify(roleParam));
      }
      this.$router.push({
        name:'role-update'
      })
    },

    init () {
      /* 渲染自定义组件 */
      let vm = this
      this.columns1.forEach(item => {
        if (item.key === 'handle') {
          item.render = (h, param) => {
            return h('div', [
               h(
                'span',
                {
                  props: {
                    type: 'text'
                  },
                  style: {
                    marginRight: '8px',
                    color:'#02E8FF',
                    cursor:'pointer',
                  },
                  on: {
                    click: function () {
                      /* 修改按钮对应的modal框 */
                      vm.viewSysten(param.row)
                    }
                  }
                },
                "操作员列表"
              ),
              h(
                'span',
                {
                  props: {
                    type: 'text',
                    // disabled: this.isGray
                  },
                  style: {
                    marginRight: '8px',
                    color: this.isGray?'gray':'#02E8FF',
                    // cursor:'pointer',
                    cursor:this.isGray?'not-allowed':'pointer',
                  },
                  on: {
                    click: function () {
                      if(vm.isGray){
                        return false;
                      }
                      vm.updateRoleInfo(param.row);
                    }
                  }
                },
                "修改"
              ),
              h(
                'Poptip',
                {
                  props: {
                    confirm: true,
                    title: "您确定要删除这条数据吗？",
                    transfer: true
                  },
                  on: {
                    'on-ok': () => {
                      vm.removeByID(param.row)
                    }
                  }
                },
                [
                  h(
                    'span',
                    {
                      style: {
                        marginRight: '0',
                        color:'#02E8FF',
                        cursor:'pointer',
                        display:this.isGray?'none':''
                      }
                    },
                    "删除"
                  )
                ]
              ),
              h('span',{
                style:{
                  marginRight: '0',
                  color:'gray',
                  cursor:'not-allowed',
                  display:this.isGray?'':'none'
                }
              }, '删除')
            ]);
          }
        }
      })
    },
    //打开操作员列表
    viewSysten(row){
      this.handlerLoading = true
      //每次打开操作员列表清空
      this.data1 = []
      // console.log('角色',row)
      this.roleName = row.name
     //配置参数
      let param = {}
      param.id = row.id
      param.roleId = row.roleId
      //调取接口2.3.6查看角色关联操作员
      api.roleAndHandler(param).then(res => {
        if(res.status && res.status == 1222){
          this.getTableList()
          this.modal5 = false
          return false
        }
        this.modal5 = true
        var result = res.result
        // console.log('结果',result)
        for(var item of result){
          var obj = {}
          obj.roleName = item.username
          obj.userId = item.userId
          obj.id = item.id
          obj.isUse = item.isEnabled === true ? "已启用" : "未启用"
          this.data1.push(obj)
        }
        this.handlerLoading = false
      },err => {
        this.handlerLoading = false
      })
    },
    //操作员列表页面的详情页
    showDetail(row){
      this.modal6 = true
      this.modal5 = false
      this.handlerDetailLoading = true
      //配置参数
      let param ={
        id:row.id,
        userId:row.userId
      }
      //调用2.2.11接口查看操作员详情
      api.specificJson(param).then(res => {
         let result = res.result
         //将操作员关联的角色信息全部拿出来
          var str =''
          for(var item of result.roles){
            str += item.roleName+' / '
         }
         let len = str.length-2
         str = str.substring(0,len)
         //给详情页面赋值
         this.formItem.handlerId = result.id
         this.formItem.handleName = result.name
         this.formItem.status = result.isEnabled ===true ? "已启用" : "未启用"
         this.formItem.iphone = result.phone
         this.formItem.email = result.email
         this.formItem.role = str
         this.formItem.createTime = result.createDate
         this.formItem.lastLoginTime = result.loginDate
         this.formItem.lastLoginIp = result.loginIp
         this.handlerDetailLoading = false
      },err => {
        this.handlerDetailLoading = false
      })
    },
    //关闭操作员详情页清空表单
    shutHandlerDetaile(name){
      this.modal5 = true
      // this.$refs[name].resetFields()
    },
    getTableList () {
      let param = {
        roleName: this.searchConName3,
        description: '',
        status: 0,   // V2.0 版本 需过滤掉 临时角色（自定义权限）   0：查询 永久角色，1：查询 临时角色
        pageNo: this.pageNumber,
        pageSize: this.pageSize
      }
      this.loading = true
      api.getRoleList(param).then(res => {
        this.dataCount = res.count
        res = res.result
        for (var i = 0; i < res.length; i++) {
          this.traverseTree(res[i])
        }
        this.historyData = this.searchResult
        // 数据获取后修改loading状态
        this.loading = false
        this.loadingBtn = false
        this.searchResult = []
        if(this.historyData.length === 0 && this.pageNumber > 1){
          this.pageNumber--
          this.getTableList()
        }
      }, err => {
        this.loading = false
        this.loadingBtn = false
        // this.$Message.error('失败' + err.errorMessage)
      })
    },
    handleSearch3Btn () {
      this.loadingBtn = true
      this.pageNumber = 1
      this.getTableList()
    },
    handleSearch3 () {
      this.loadingBtn = true
      // this.pageNumber = 1
      this.getTableList()
    },
    /* 改变标签页码 */
    changePage (index) {
      // var _start = (index - 1) * this.pageSize
      // var _end = index * this.pageSize
      // this.historyData = this.ajaxHistoryData.slice(_start, _end)
      // this.current = index
      this.pageNumber = index
      this.getTableList()
      // this.pageNumber = 1
    },
    /* 每页显示条目数量 对应更新显示 */
    changePageSize (pagesize) {
      // this.pageSize = pagesize
      // if (this.ajaxHistoryData.length < this.pageSize) {
      //   this.historyData = this.ajaxHistoryData
      // } else {
      //   this.historyData = this.ajaxHistoryData.slice(0, this.pageSize)
      // }
      this.pageSize = pagesize
      if (this.pageNumber === 1) this.getTableList()
    },
    /* 根据ID删除数据 */
    removeByID (val) {
      // for (var i = this.ajaxHistoryData.length - 1; i >= 0; i--) {
      //     if (this.ajaxHistoryData[i].id === val) this.ajaxHistoryData.splice(i, 1);
      // }
      // console.log(val)
      let id = val.id.toString()
      let roleId = val.roleId.toString()
      let param = {
        ids: [id],
        roleIds: [roleId]
      }
      this.loading = true
      api.deleteRole(param).then(res => {
        //添加数据延迟判断
        if(res.status && res.status == 1222){
           this.getTableList()
           return false
        }
        this.$Message.success("角色删除成功")
        if (this.dataCount % this.pageSize === 1 && this.pageNumber === Math.ceil(this.dataCount / this.pageSize) && this.pageNumber > 1) {
          this.pageNumber--
        }
        this.getTableList()
      }, err => {
        this.loading = false
      })
    },
    /* 根据输入框的信息 包装一条数据插入到数组中 */
    submitInfo (name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          let param = {
            name: this.formValidate.newName,
            description: this.formValidate.newDescription
          }
          this.loading = true
          api.creatRole(param).then(res => {
            this.$Message.success("角色创建成功")
            this.getTableList()
          }, err => {
            this.loading = false
            this.getTableList()
          })
          this.modal1 = false
          this.formValidate.newName = ''
          this.formValidate.newDescription = ''
        } else {
          this.$Message.error("请输入符合规则的内容")
        }
      })
      // if (this.formValidate.newName === '' || this.formValidate.newDescription === '') {
      //   this.$Message.error('用户名或者角色描述为空!')
      // } else {
      //   /* 接口部分 */
      // }
    },
    /* 获取旧数据 显示 */
    updateInfo (src) {
      // this.reset('formValidate')
      this.selectedItem = src
      this.formValidate1.oldName = src.name
      this.formValidate1.oldDescription = src.description
    },
    /* 数据更新 */
    updateInfo1 (name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          /* 对接接口部分 */
          let param = {
            id: this.selectedItem.id,
            roleId: this.selectedItem.roleId,
            name: this.formValidate1.oldName,
            description: this.formValidate1.oldDescription
          }
          this.loading = true
          api.updateRole(param).then(res => {
            this.$Message.success("修改角色成功")
            this.getTableList()
          }, err => {
            this.getTableList()
            this.loading = false
          })
          this.modal2 = false
        } else {
          this.$Message.error("请输入符合规则的内容")
        }
      })
    },
    /* 指定格式的时间字符串 yyyy-mm-dd hh:mm:ss */
    getNowFormatDate () {
      var date = new Date()
      var seperator1 = '-'
      var seperator2 = ':'
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate =
        date.getFullYear() +
        seperator1 +
        month +
        seperator1 +
        strDate +
        ' ' +
        date.getHours() +
        seperator2 +
        date.getMinutes() +
        seperator2 +
        date.getSeconds()
      return currentdate
    },
    /* modal框取消 */
    cancelModal () {
      this.reset('formValidate1')
      this.formValidate.newName = ''
      this.formValidate.newDescription = ''
      this.modal1 = false
      this.modal2 = false
    },
    /* 遍历查询结果 */
    traverseNode (node) {
      var item = {
        id: node.id,
        roleId: node.roleId,
        name: node.name,
        description: node.description,
        createDate: node.createDate,
        operationPermissions: node.operationPermissions,
        // isEnable: node.isEnable === '1' ? '已启用' : '未启用'
      }
      this.searchResult.push(item)
    },
    traverseTree (node) {
      if (!node) {
        return
      }
      var stack = []
      stack.push(node)
      var tmpNode
      while (stack.length > 0) {
        tmpNode = stack.pop()
        this.traverseNode(tmpNode)
        if (tmpNode.permission && tmpNode.permission.length > 0) {
          var i = tmpNode.permission.length - 1
          for (i = tmpNode.permission.length - 1; i >= 0; i--) {
            stack.push(tmpNode.permission[i])
          }
        }
      }
    },
    /* 重置表单 */
    reset (name) {
      // alert()
      this.$refs[name].resetFields()
    },
    
  }
}
</script>
